<template>
    <div class="goodsinfo-container">
        <transition 
            @before-enter="beforeEnter"
            @enter="enter"
            @after-enter="afterEnter">
            <div class="ball" v-show="ballFlag" ref="ball"></div>
        </transition>
        <!-- 商品轮播图区域 -->
        <div class="mui-card">
            <div class="mui-card-content">
                <div class="mui-card-content-inner">
                    <lunbotu  :lunboTu="list" :isfull="false"/>
                </div>
            </div>
        </div>
       <!-- 商品购买区域 -->
        <div class="mui-card">
            <div class="mui-card-header">{{goodsInfo.title}}</div>
            <div class="mui-card-content">
                <div class="mui-card-content-inner">
                    <p>
                        市场价：<del>￥{{goodsInfo.market_price}}</del>&nbsp;&nbsp;销售价：<span class="now_price">￥{{goodsInfo.sell_price}}</span>
                    </p>
                    <div class="shopcar">
                        <span>购买数量：</span>
                        <numbox @getCount="getSelectedCount" :min='1' :max='goodsInfo.stock_quantity'/>
                        
                    </div>
                    <p>
                        <mt-button size="small" type="primary">立即购买</mt-button>
                        <mt-button size="small" type="danger" @click="addToshopCar">加入购物车</mt-button>
                    </p>
                </div>
            </div>
            
        </div>
        <!-- 商品参数区域 -->
        <div class="mui-card">
            <div class="mui-card-header">商品参数</div>
            <div class="mui-card-content">
                <div class="mui-card-content-inner">
                    <p>商品货号：{{goodsInfo.goods_no}}</p>
                    <p>库存情况：{{goodsInfo.stock_quantity}}</p>
                    <p>上架时间：{{goodsInfo.add_time | dateFormat}}</p>
                </div>
            </div>
            <div class="mui-card-footer">
                <mt-button size="large" type="primary" plain @click="goDesc">图文介绍</mt-button>
                <mt-button size="large" type="danger" plain @click="goComments">商品评论</mt-button>
            </div>
        </div>
    </div>
</template>
<script>
import { Toast } from 'mint-ui'
import api from '@/api/index.js'
import Lunbotu from '@/components/home/lunbotu'
import numbox from '@/components/shopcar/shopcar'

export default {
    data() {
        return {
            list:[],//轮播图图片列表
            goodsInfo:{},//商品参数
            ballFlag:false,//控制小球的隐藏和显示的标识符
            selectedCount:1//保证用户选中的商品数量，默认为1
        }
    },
    created() {
        const goodsId = this.$route.params.id;
        this.getThumimages(goodsId);
        this.getGoodsInfo(goodsId);
    },
    methods: {
         //获取图片缩略图数组
        getThumimages(id) {
            api.getThumimages(id).then(res => {
                if(res.data.status === 0) {
                    //询函每个图片，补全图片的宽和高
                    res.data.message.forEach(item => {
                        item.img_url= item.src;
                    });
                    // 把完整的数据保存到list中
                    this.list = res.data.message;
                }
            })
        },
        //商品文字信息
        getGoodsInfo(goodsId){
            api.getGoodsInfo(goodsId).then(res => {
                if(res.data.status === 0) {
                    this.goodsInfo = res.data.message[0];
                }
            })
        },
        //图文介绍页面
        goDesc() {
            const goodsid = this.$route.params.id;
            this.$router.push({ name:'goodsdesc',params: { id: goodsid }})
            
        },
        //评论页面
        goComments() {
            const goodsid = this.$route.params.id;
            this.$router.push({ name:'goodscomments',params: { id: goodsid }})
        },
        //加入购物车
        addToshopCar() {
            this.ballFlag = !this.ballFlag;
            //{id:商品id，count：要购买的数量，price：商品的单价，seleced：是否被选中}
            var goodsinfo = {
                    id:this.$route.params.id,
                    count:this.selectedCount,
                    price:this.goodsInfo.sell_price,
                    selected:true
                }

            //调用 store中的mutations 来将商品加入购物车
            this.$store.commit('addToCar',goodsinfo);
        },
        beforeEnter(el) {
            el.style.transform = "translate(0,0)";
        },
        enter(el,done) {
            el.offsetWidth;
            //获取小球在页面中的位置
            const ballPosition = this.$refs.ball.getBoundingClientRect();
            //获取徽标在页面中的位置
            const badgePosition = document.getElementById('badge').getBoundingClientRect();

            const xDist = badgePosition.left - ballPosition.left;
            const yDist = badgePosition.top - ballPosition.top;

            el.style.transform = `translate(${xDist}px,${yDist}px)`;
            el.style.transition = "all 0.5s cubic-bezier(.4,-0.3,1,.68)";
            done();
        },
        afterEnter(el) {
            this.ballFlag = !this.ballFlag;
        },
        getSelectedCount(params) {
            //当子组件把选中的数量传递给父组件的时候,把选中的值保存到data上
            // this.selectedCount = count;
            console.log(params);
            this.selectedCount = params.count;
        }

    },
    components:{
        Lunbotu,
        numbox
    }
}
</script>
<style lang="scss" scoped>
    .goodsinfo-container{
        background:#eee;
        overflow:hidden;
        .now_price{
            color:red;
            font-size:16px;
            font-weight:bold;
        }
        /deep/ .mint-button label{
            margin-bottom:0px !important;
        }
        .shopcar{
            margin-bottom:10px;
        }
        .mui-card-footer{
            display:block;
            button{
                margin:15px 0px;
            }
        }
        .ball{
            width:15px;
            height:15px;
            border-radius:50%;
            background:red;
            position:absolute;
            z-index:99;
            top:390px;
            left:150px;
        }
    }
</style>


