<template>
    <div class="mui-numbox" data-numbox-min='1' data-numbox-max='9'>
        <button class="mui-btn mui-btn-numbox-minus" type="button" @click="jianGoods">-</button>
        <input id="test" class="mui-input-numbox" type="number" v-model="count" @input="countChanged"/>
        <button class="mui-btn mui-btn-numbox-plus" type="button" @click="addGoods">+</button>
    </div>
</template>
<script>
import { Toast } from 'mint-ui'

export default {
    props:[
        'min',
        'max',
    ],
    data() {
        return {
            count:1,
        }
    },
    methods: {
        addGoods() {
            if(this.count < this.max) {
                this.count ++;
            } else {
                Toast('最多购买'+ this.max +'个');
            }
            this.$emit('getCount',{count:this.count});
        },
        jianGoods(){
            if(this.count > 1) {
                this.count --;
            }
            this.$emit('getCount',{count:this.count});
        },
        countChanged() {
            console.log('ghjk',this.count);
            if(this.count > this.max) {
               Toast('最多购买'+ this.max +'个');
               this.count = this.max;
            }
            this.$emit('getCount',{count:this.count});
            
        }
    }
}
</script>


